:root {
  // Custom variable values only support SassScript inside `#{}`.
  @each $color, $value in $colors-alt {
    --#{$variable-prefix}#{$color}-alt: #{$value};
  }

  @each $color, $value in $grays-alt {
    --#{$variable-prefix}gray-#{$color}-alt: #{$value};
  }

  @each $color, $value in $theme-colors-alt {
    --#{$variable-prefix}#{$color}-alt: #{$value};
  }

  @each $color, $value in $theme-colors-rgb-alt {
    --#{$variable-prefix}#{$color}-rgb-alt: #{$value};
  }

  --#{$variable-prefix}white-rgb-alt: #{to-rgb($white-alt)};
  --#{$variable-prefix}black-rgb-alt: #{to-rgb($black-alt)};
  --#{$variable-prefix}body-color-rgb-alt: #{to-rgb($body-color-alt)};
  --#{$variable-prefix}body-bg-rgb-alt: #{to-rgb($body-bg-alt)};

  // root and body

  --#{$variable-prefix}body-color-alt: #{$body-color-alt};
  --#{$variable-prefix}body-bg-alt: #{$body-bg-alt};

  // ?

  --#{$variable-prefix}gradient-alt: #{$gradient-alt};
}
